/* EditProfile.css */

.edit-profile {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(5px);
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .edit-profile .edit-profile-popup {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    width: 400px;
    text-align: center;
  }
  .edit-profile .edit-profile-popup .card-header{
    display: flex;
    justify-content: space-between;
  }
  .edit-profile .edit-profile-popup .close-button {
    height: 40px;
    width: 40px;
    color: #DEDEDE;
    background-color: rgb(39, 39, 78);
    border: none;
    border-radius: 50%;
    font-size: 18px;
    cursor: pointer;
  }
  
  .edit-profile .edit-profile-popup h2 {
    margin-bottom: 20px;
  }
  
  .edit-profile .edit-profile-popup form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .edit-profile .edit-profile-popup label {
    margin: 10px 0;
    font-weight: bold;
  }
  
  .edit-profile .edit-profile-popup input[type="text"],
  .edit-profile .edit-profile-popup input[type="password"] {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .edit-profile .edit-profile-popup input[type="file"] {
    margin-top: 5px;
  }
  
  .edit-profile .edit-profile-popup img {
    max-width: 100%;
    margin-top: 10px;
    border-radius: 5px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  .edit-profile .edit-profile-popup button[type="submit"] {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .edit-profile .edit-profile-popup  button[type="submit"]:hover {
    background-color: #0056b3;
  }
  
  .edit-profile .edit-profile-popup .success-message {
    color: green;
    margin-top: 10px;
  }
  
  .edit-profile .edit-profile-popup  .error-message {
    color: red;
    margin-top: 10px;
  }
  